import React, { useState, useEffect } from "react";
import Sidebar from "../../../components/Admin/Sidebar";
import TopBar from "../../../components/Admin//TopBar";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { Link } from "react-router-dom";
import DataTable from "react-data-table-component";
import { FaPlus } from "react-icons/fa6";
import SuccessAlert from "../../../components/Admin/SuccessAlert";
function AdminDiscountCode() {
  const navigate = useNavigate();
  const [records, setRecords] = useState([]);
  useEffect(() => {
    document.title = "Amount Add DataRoom, Extra Due Diligence - Admin";
  }, []);
  const apiUrl = "https://blueprintcatalyst.com/api/admin/module/";
  const [searchQuery, setSearchQuery] = useState("");

  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  const columns = [
    {
      name: "Code",
      selector: (row) => row.name,
      sortable: true,
      className: "age",
    },
    {
      name: "Amount",
      selector: (row) => row.subcategory_count,
      sortable: true,
      className: "age",
    },
    {
      name: "Payment Type",
      selector: (row) => row.subcategory_count,
      sortable: true,
      className: "age",
    },
    {
      name: "Status",
      selector: (row) => row.subcategory_count,
      sortable: true,
      className: "age",
    },
    {
      name: "Share",
      selector: (row) => row.subcategory_count,
      sortable: true,
      className: "age",
    },
  ];
  return (
    <div>
      <div className="d-flex align-items-start gap-0">
        <Sidebar />
        <div className="d-flex flex-column gap-0 w-100">
          <TopBar />
          <section className="dashboard_adminh">
            <div className="container-xl">
              <div className="row gy-4">
                <div className="col-12">
                  <div className="card p-4">
                    <div className="d-flex justify-content-between mb-3">
                      <h5 className="mb-3">Discount Code</h5>
                      <Link
                        to="/admin/setting/createpaymentdiscount"
                        className="btn btn-primary"
                      >
                        Add Code <FaPlus />
                      </Link>
                    </div>
                    <div className="d-flex justify-content-end align-items-end">
                      <div class="search-bar">
                        <input
                          type="text"
                          placeholder="Search..."
                          value={searchQuery}
                          onChange={(e) => setSearchQuery(e.target.value)}
                        />
                        <span class="search-icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                          >
                            <path
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              stroke-width="2"
                              d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                            />
                          </svg>
                        </span>
                      </div>
                    </div>
                    <DataTable
                      columns={columns}
                      data={filteredRecords}
                      pagination
                      highlightOnHover
                      striped
                      responsive
                      noDataComponent={
                        <div className="text-center">
                          <span>No results found</span>
                        </div>
                      }
                    />
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}

export default AdminDiscountCode;
